/**
 * @hidden
 */
export class CustomError extends Error {
  public readonly data: any;

  // istanbul ignore next
  constructor({
    name = "",
    message = "",
    data
  }: {
    name?: string;
    message?: string;
    data?: any;
  } = {}) {
    super(message);
    Object.setPrototypeOf(this, CustomError.prototype);
    this.name = name;
    this.data = data;
  }
}
